import React, { useEffect, useState } from 'react';
//antd imports
import { Form, Input, Select, Button, message, Spin, Col, Row } from 'antd';
import { UserOutlined, LockOutlined, MailOutlined } from '@ant-design/icons';
//router
import { Link, Redirect } from 'react-router-dom';
//axios
import axios from 'axios';
//animation
import { motion } from 'framer-motion';
//assets
import RegistrationImg from '../../assets/registration.svg';
import logo from '../../assets/logo.png';
import popAnimation from '../../assets/Animations/signupSuccess.png';
//helmet
import { Helmet } from 'react-helmet';
const { Option } = Select;
const formItemLayout = {
  labelCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 8,
    },
  },
  wrapperCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 24,
    },
  },
};
const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

// import { useHistory } from 'react-router-dom';
// let history = useHistory();
// history.push('/login');

const Signup = () => {
  const [spin, setSpin] = useState(false);
  const [showLogin, setShowLogin] = useState(true);
  const [popConfirm, setPopConfirm] = useState(false);
  const [form] = Form.useForm();

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (token == null) {
      setShowLogin(true);
    } else {
      setShowLogin(false);
    }
  }, []);

  const onFinish = (values) => {
    setSpin(true);
    const formData = {
      fullname: values.Name,
      role: values.role,
      email: values.email,
      mobile: values.phone,
      password: values.password,
    };

    axios
      .get(`https://ginnovation-server.herokuapp.com/api/admin/token`)
      .then((resp) => {
        setSpin(false);
        console.log(resp.data[0].accesstoken);
        if (resp.data[0].accesstoken === values.code) {
          message.success('Verification Successful');
          axios
            .post(
              `https://ginnovation-server.herokuapp.com/api/register`,
              formData
            )
            .then((resp) => {
              setSpin(false);
              if (resp.data.status === 'success') {
                message.success('Registration Complete');
                setPopConfirm(true);
              } else {
                message.warning('Oops! Something went wrong');
              }
            })
            .catch((err) => {
              setSpin(false);
              console.log(err.response);
              if (err.response.data.status === 'exists') {
                message.warning('This data already exists!');
              } else {
                message.error(err.response.data.message);
              }
            });
        } else {
          message.error('Verification Failed');
        }
      })
      .catch((err) => {
        setSpin(false);
      });
  };

  const prefixSelector = (
    <Form.Item name='prefix' noStyle>
      <Select
        style={{
          width: 90,
        }}
      >
        <Option value='91'>+91</Option>
        <Option value='977'>+977</Option>
      </Select>
    </Form.Item>
  );
  //
  return (
    <>
      <Helmet>
        <title>Signup - Ginnovation</title>
        <meta
          name='description'
          content='Welcome to Ginnovation CLub | Enter your basic details to register yourself to the world of innovation. Ginnovation welcomes you to explore together.'
        />
      </Helmet>
      {showLogin ? (
        <div className='login-page'>
          {spin && (
            <div className='modal-container'>
              <Spin size='large' />
            </div>
          )}
          {popConfirm ? (
            <div className='modal-container'>
              <div className='pop-confirm registration'>
                <img src={popAnimation} alt='registration success' />
                <p>You have Registered Successfully</p>
                <h4>Just a step remain ;)</h4>
                <div className='btn-container'>
                  <Link to='/login'>
                    <button>Headback to LogIn</button>
                  </Link>
                </div>
              </div>
            </div>
          ) : (
            ''
          )}
          <div className='login-container '>
            <Row className='row'>
              <Col md={12} className='col-div'>
                <motion.div
                  initial={{ x: -1000 }}
                  animate={{ x: 0 }}
                  transition={{ delay: 0.5 }}
                  className='image signup'
                >
                  <img src={RegistrationImg} alt='ginnovation registration' />
                </motion.div>
              </Col>
              <Col md={12} className='col-div'>
                <motion.div
                  initial={{ x: 1000 }}
                  animate={{ x: 0 }}
                  transition={{ delay: 0.5 }}
                  className='content'
                >
                  <img
                    src={logo}
                    alt='ginnovation registration'
                    className='for-signup'
                  />
                  <div className='login-board'>
                    <header>
                      <h2>Sign Up</h2>
                      <small>Enter the details to register</small>
                    </header>
                    <Form
                      {...formItemLayout}
                      form={form}
                      name='register'
                      onFinish={onFinish}
                      initialValues={{
                        prefix: '91',
                      }}
                      scrollToFirstError
                    >
                      <Row gutter={10}>
                        <Col md={24}>
                          <Form.Item
                            name='Name'
                            rules={[
                              {
                                required: true,
                                message: 'Please input your Name!',
                              },
                            ]}
                          >
                            <Input
                              prefix={
                                <UserOutlined className='site-form-item-icon' />
                              }
                              placeholder='Full Name'
                            />
                          </Form.Item>
                        </Col>

                        <Col md={12}>
                          <Form.Item
                            name='phone'
                            rules={[
                              {
                                required: true,
                                message: 'Please input your phone number!',
                              },
                            ]}
                          >
                            <Input
                              addonBefore={prefixSelector}
                              placeholder='Mobile Number'
                            />
                          </Form.Item>
                        </Col>
                        <Col md={12}>
                          <Form.Item
                            name='email'
                            rules={[
                              {
                                type: 'email',
                                message: 'The input is not valid E-mail!',
                              },
                              {
                                required: true,
                                message: 'Please input your E-mail!',
                              },
                            ]}
                          >
                            <Input
                              prefix={
                                <MailOutlined className='site-form-item-icon' />
                              }
                              placeholder='Email'
                            />
                          </Form.Item>
                        </Col>
                        <Col md={24}>
                          <Form.Item
                            name='role'
                            rules={[
                              {
                                required: true,
                                message: 'Please select a role!',
                              },
                            ]}
                          >
                            <Select placeholder='Select your role'>
                              <Option value='Student'>Student</Option>
                              <Option value='Teacher'>Teacher</Option>
                            </Select>
                          </Form.Item>
                        </Col>
                        <Col md={12}>
                          <Form.Item
                            name='password'
                            rules={[
                              {
                                required: true,
                                message: 'Please input your password!',
                              },
                            ]}
                            hasFeedback
                          >
                            <Input.Password
                              prefix={
                                <LockOutlined className='site-form-item-icon' />
                              }
                              placeholder='Password'
                            />
                          </Form.Item>
                        </Col>
                        <Col md={12}>
                          <Form.Item
                            name='confirm'
                            dependencies={['password']}
                            hasFeedback
                            rules={[
                              {
                                required: true,
                                message: 'Please confirm your password!',
                              },
                              ({ getFieldValue }) => ({
                                validator(rule, value) {
                                  if (
                                    !value ||
                                    getFieldValue('password') === value
                                  ) {
                                    return Promise.resolve();
                                  }

                                  return Promise.reject(
                                    'The two passwords that you entered do not match!'
                                  );
                                },
                              }),
                            ]}
                          >
                            <Input.Password
                              prefix={
                                <LockOutlined className='site-form-item-icon' />
                              }
                              placeholder='Confirm Password'
                            />
                          </Form.Item>
                        </Col>

                        <Col md={24}>
                          <Form.Item
                            name='code'
                            rules={[
                              {
                                required: true,
                                message: 'Please input your verification code!',
                              },
                            ]}
                          >
                            <Input placeholder='Verification Code' />
                          </Form.Item>
                        </Col>
                      </Row>

                      <div {...tailFormItemLayout} className='form-btn-row'>
                        <div className='register-btn-container'>
                          <Button type='primary' htmlType='submit'>
                            Register
                          </Button>
                          Or
                          <Link to='/login'>
                            <button className='trans-btn'>
                              You are already a member!
                            </button>
                          </Link>
                        </div>
                      </div>
                    </Form>
                  </div>
                </motion.div>
              </Col>
            </Row>
          </div>
        </div>
      ) : (
        <Redirect to='/' />
      )}
    </>
  );
};
export default Signup;
